<template>
  <div>
    <!-- TODO -->
    <div class="paging_step">
      <span class="ico_join ico_paging"></span>
      <span class="ico_join ico_paging on"></span>
    </div>
      <br>
         .

    <div class="form-group row justify-content-md-center">
      <div>
        <dl>
          <dt><label></label></dt>
          <dd>
            <div>
              <!-- <input type="text" class="form-control" id="userId" required v-model="$v.userId.$model" placeholder="  " name="userId" @focus="statusChange('id')" @blur="duplicateIdCheckHandler"> -->
              <input type="text" class="form-control" id="userId" required v-model="$v.userId.$model" placeholder="  " name="userId" >
            </div>
            <div v-if="!$v.userId.required">
              <p class="txt_message">  .</p>
            </div>
            <!-- <div v-if="idDuplicateChk"> -->
            <div v-else-if="!$v.userId.isUnique">
              <p class="txt_message">      .    .</p>
            </div>
            <!-- <div v-else-if="idLengthChk"> -->
            <div v-else-if="!$v.userId.minLength">
              <p class="txt_message"> !  3 .</p>
            </div>
            <!-- <div v-else-if="idOnlyNumChk"> -->
            <div v-else-if="!$v.userId.numeric">
              <p class="txt_message">     .      .</p>
            </div>
            <!-- <div v-else-if="idValidChk"> -->
            <!-- <div v-else-if="!$v.userId.spcHelpers || !$v.userId.dotHelpers"> -->
            <div v-else-if="!$v.userId.helper">
              <p class="txt_message">  , , (-), (_), (.)   .
                (, (.)      )</p>
            </div>
            <div v-else-if="!$v.userId.bigHelper">
              <p class="txt_message">    .    .</p>
            </div>
          </dd>
        </dl>
        <dl>
          <dt><label></label></dt>
          <dd>
            <div>
              <!-- <input type="password" class="form-control" id="password1" required v-model="$v.password1.$model" maxlength="32" placeholder="(8 )" name="password1" @focus="statusChange('password1')" @blur="passwordValidCheck"> -->
              <input type="password" class="form-control" id="password1" required v-model="$v.password1.$model" maxlength="32" placeholder="(8 )" name="password1">
            </div>
            <!-- <div v-if="passwordOnlyNum"> -->
            <div v-if="!$v.password1.numeric">
              <p class="txt_message">     ! ,    .</p>
            </div>
          </dd>
          <dt><label> </label></dt>
          <dd>
            <div>
              <!-- <input type="password" class="form-control" id="password2" required v-model="$v.password2.$model" maxlength="32" placeholder=" " name="password2" @focus="statusChange('password2')" @blur="compareTwoPassword"> -->
              <input type="password" class="form-control" id="password2" required v-model="$v.password2.$model" maxlength="32" placeholder=" " name="password2">
            </div>
            <!-- <div v-if="isMatchedPwd"> -->
            <div v-if="!$v.password2.sameAsPassword">
              <p class="txt_message">  .   .</p>
            </div>
          </dd>
        </dl>
      </div>

      <div>
        <dl>
          <dt><label></label></dt>
          <dd>
            <div>
              <!-- <input type="text" class="form-control" id="userName" required v-model="$v.userName.$model" placeholder="" maxlength="32" name="userName"  @focus="statusChange('name')" @blur="nameValidCheck"> -->
              <input type="text" class="form-control" id="userName" required v-model="$v.userName.$model" placeholder="" maxlength="32" name="userName">
            </div>
            <div v-if="!$v.userName.required">
              <p class="txt_message">  .</p>
            </div>
            <!-- <div v-if="nameOnlyStr"> -->
            <div v-else-if="!$v.userName.helpers"> 
              <p class="txt_message">    . (   )    .</p>
            </div>
            <!-- <div v-else-if="nameKoLengthChk"> -->
            <div v-else-if="!$v.userName.isKorean">
              <p class="txt_message"> (2  3 )   .</p>
            </div>
            <!-- <div v-else-if="nameEnLengthChk"> -->
            <div v-else-if="!$v.userName.isAlpha">
              <p class="txt_message">  2~30    .</p>
            </div>
          </dd>
        </dl>
        <dl>
          <dt><label> </label></dt>
          <dd>
            <div>
              <!-- <input type="text" class="form-control" id="email" required v-model="$v.email.$model" placeholder="" name="email" @focus="statusChange('email')" @blur="emailValidCheck"> -->
              <input type="text" class="form-control" id="email" required v-model="$v.email.$model" placeholder="" name="email">
            </div>
            <!-- <div v-if="emailValidChk"> -->
            <div v-if="!$v.email.email">
              <p class="txt_message">   .      .</p>
            </div>
          </dd>
        </dl>
      </div>

      <!-- TODO captcha key, image     -->
      <div>
        <dl>
          <dt>
            <img :src="`/static/images/captchaImage/${captchaImageName}.jpg`">
            <input type="text" v-model="captchaValue">
            <button @click="captchaValidCheck">captchaValidCheck</button>
          </dt>
        </dl>
      </div>
    </div>
    <!-- <pre>{{$v.$invalid}}</pre> -->
    <router-link class="btn btn-sm btn-outline-warning" to="/join/joinTerms"></router-link>
    <!-- TODO   ->  ?  ..?? ..?-->
    <router-link to="/">
      <button @click="saveMember" :disabled="invalidChecker == 1" class="btn btn-sm btn-info"> </button>
    </router-link>
  </div>
</template>

<script>
/* TODO */
import { axiosInstanceMember, axiosInstanceCaptcha } from '../../http-common'
import { required, minLength, sameAs, helpers, email, numeric, not, async} from 'vuelidate/lib/validators'

export default {
  name: "signUp",
  data() {
    return {
      userId: '',
      password1: '',
      password2: '',
      userName: '',
      email: '',
      /* id check value */
      responsedata: '',
      idDuplicateChk: false,
      idValidChk: false,
      idLengthChk: false,
      idOnlyNumChk: false,
      /* password check value */
      isMatchedPwd: false,
      passwordOnlyNum: false,
      /* name check value */
      nameOnlyStr: false,
      nameKoLengthChk: false,
      nameEnLengthChk: false,
      /* email check value */
      emailValidChk: false,
      /* captcha */
      captchaKey: '',
      captchaValue: '',
      captchaImageName: '',
    }
  },
  created() {
    this.$nextTick(() => {
      this.getCaptchaKey()
    })
  },
  computed: {
    invalidChecker() {
      if(this.$v.$invalid) return 1
      else return -1
    }
  },
  validations: {
    userId: {
      required,
      isUnique(userId) {
        // if (userId === '') return true
        return axiosInstanceMember
          .post("/check/" + this.userId)
          .then(response => {
            if (response.data == "EXIST")  return false
            else return true
          })
          .catch(error => {
            console.log(error)
          })
      },
      minLength: minLength(3),
      numeric: not(numeric),
      helper: not(helpers.regex('userId', /[`~!@#$%^&*()=+\\|;:'",<>/?[\]{}]|^\.{1,}|[\.]$|\.{2,}/)),
      bigHelper: not(helpers.regex('userId', /[A-Z]/))
    },
    password1: {
      numeric: not(numeric)
    },
    password2: {
      sameAsPassword: sameAs('password1')
    },
    userName: {
      required,
      helpers: not(helpers.regex('userName', /[0-9]|[`~!@#$%^&*()\-_=+\\|;:'",<.>/?[\]{}]|\s/)),
      isKorean(userName){
        if (/[-|-|-]/.test(userName) && userName.length < 2) return false
        else return true
      },
      isAlpha(userName) {
        if (/[a-zA-Z]/.test(userName) && userName.length < 2) return false
        else return true
      }
    },
    email: {
      email
    }
  },
  methods: {
    saveMember() {
      var memberData = {
        userId: this.userId,
        name: this.userName,
        password: this.password1,
        email: this.email,
      }
      
      this.$v.$touch()
      if (!this.$v.$invalid) {
        axiosInstanceMember
        .put("/join", memberData)
        .then(response => {
          this.$noty.success("  !", {
            theme: 'semanticui',
            timeout: 3000
          })
        })
        .catch(e => {
          console.log(e);
        });
      }
    },
    /* TODO captcha */
    getCaptchaKey() {
      axiosInstanceCaptcha
      .get("/key")
      .then(response => {
        this.captchaKey = response.data.key
        console.log("*** GET captchaKey success ***", response.data.key)
        this.getCaptchaImage()
      })
      .catch(e => {
        console.log(e)
      })
    },
    getCaptchaImage() {
      axiosInstanceCaptcha
      .get("/image/" + this.captchaKey)
      .then(response => {
        this.captchaImageName = response.data
        console.log("*** GET captchaImage success ***", response.data)
      })
      .catch(e => {
        console.log(e)
      })
    },
    captchaValidCheck() {
      axiosInstanceCaptcha
      .get("/check/" + this.captchaKey + "/" + this.captchaValue)
      .then(response => {
        console.log(response.data.result)
      })
      .catch(e => {
        console.log(e)
      })
    },

  }
}
</script>

<style src="@/assets/styles/style.css">
/* @import '../../../../frontend/src/assets/styles/style.css'; */
</style>


